@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: 'Bebas Neue', cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #ffffff;  
}

h1, h2, h3, h4, p{
  font-weight: lighter;
}

p{
  font-family: 'Poppins', sans-serif;  
  font-size: 18px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: transparent; 
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #888; 
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555; 
}





.App {
  text-align: center; 
  position: relative; 
  width: 100%;
  height: auto;
  margin: 0 auto;
}

.container{
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;    
}

#header{
  width: 245px;
  height:100vh;  
}

.page-header{
  box-sizing: content-box;
  width: 100%;    
}

.page-header h1, h3{
  margin: 0px;  
}

.page-header h3{
  font-family: 'Poppins', sans-serif; 
}

#personal-links{
  margin: 10px auto;
}

a{
  cursor: pointer;
  color: white;
  transition: color 0.5s;
  text-decoration: none; 
  font-size: 24px;                                             
}

a:hover{
  color: tomato;
}




.section{
    margin: 0 auto;
}
  
.section-title{
    border-top: solid 1px tomato;
    border-bottom: solid 1px tomato;
    padding: 10px;  
    text-align: center;
}

#about p{
    text-align: justify;
    margin: 10px auto;
}
  
#about, #projects{
    width: 800px;
    height: auto;  
}

#projects{ 
    padding-bottom: 50px;
}

.description b{
    color: tomato;
}

@media only screen and (max-width: 800px) {
    #about, #projects{
        width: 500px;
    }
}

@media only screen and (max-width: 500px) {
    #about, #projects{
        width: 300px;
    }
}
  
