.App {
  text-align: center; 
  position: relative; 
  width: 100%;
  height: auto;
  margin: 0 auto;
}

.container{
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;    
}

#header{
  width: 245px;
  height:100vh;  
}

.page-header{
  box-sizing: content-box;
  width: 100%;    
}

.page-header h1, h3{
  margin: 0px;  
}

.page-header h3{
  font-family: 'Poppins', sans-serif; 
}

#personal-links{
  margin: 10px auto;
}

a{
  cursor: pointer;
  color: white;
  -webkit-transition: color 0.5s;
  transition: color 0.5s;
  text-decoration: none; 
  font-size: 24px;                                             
}

a:hover{
  color: tomato;
}



